import React from 'react'
import Layout from 'react-tackle-box/Layout'

import Input from '../../components/Input'
import Log from '../../components/Log'

const propTypes = {}

class ContactInfo extends React.Component {
  state = {
    detectAutofill: true,
  }

  render() {
    return (
      <div className="container my-4">
        <label className="mt-4">
          <input
            type="checkbox"
            checked={this.state.detectAutofill}
            onChange={() =>
              this.setState({ detectAutofill: !this.state.detectAutofill })
            }
          />{' '}
          Try and detect autofill via webkit only hacks
        </label>
        <form
          method="POST"
          action="http://httpbin.org/post"
          onSubmit={this.handleSubmit}
        >
          <Layout direction="column" pad align="flex-start">
            <Layout pad align="center">
              <label htmlFor="user-field">Name</label>
              <Input
                type="text"
                name="name"
                id="user-field"
                autoComplete="full-name"
                detectAutofill={this.state.detectAutofill}
              />
            </Layout>

            <Layout pad>
              <label htmlFor="first-name">First name</label>
              <Input
                type="text"
                name="first-name"
                id="first-name"
                autoComplete="first-name"
                detectAutofill={this.state.detectAutofill}
              />
            </Layout>
            <Layout pad>
              <label htmlFor="last-name">Last name</label>
              <Input
                type="text"
                name="last-name"
                id="last-name"
                autoComplete="last-name"
                detectAutofill={this.state.detectAutofill}
              />
            </Layout>
            <Layout pad justify="flex-start">
              <button>Submit</button>
              <button type="reset">Reset</button>
            </Layout>
          </Layout>
        </form>
        <Log />
      </div>
    )
  }
}

ContactInfo.propTypes = propTypes

export default ContactInfo
